import robbyImageUpload from '../../components/robby-image-upload/robby-image-upload.vue'
import {ajax} from '../../common/js/util.js'
import toast from '../../common/js/toast.js'
import global_ from '../../common/js/global.js'
import amapFile from '../../common/js/amap-wx.js'
export default {
	data() {
		return {
			show: false,
			radio: 1,
			address: '',
			user: '',
			tel: '',
			content: '',
			imageData: [],
			url: global_.baseUrl,
			longitude: '',
			latitude: ''
		}
	},
	components: {
		robbyImageUpload
	},
	onLoad() {
		let self = this
		uni.getLocation({
			type: 'gcj02',
			success: function (res) {
				self.latitude = res.latitude
				self.longitude = res.longitude
				var myAmapFun = new amapFile.AMapWX({key:'196ae56c16a196319ccabd73b260c593'})
				myAmapFun.getRegeo({
				  location: self.longitude + ',' + self.latitude,
				  success: function(data){
					//成功回调
					console.log(data)
					self.address = data[0].name
				  },
				  fail: function(info){
					//失败回调
					console.log(info)
				  }
				})
			}
		})
	},
	methods: {
		shows() {
			this.show = true
		},
		RadioChange(e) {
			this.radio = e.detail.value
			this.show = false
		},
		deleteImage(obj) {
			console.log(obj)
		},
		addImage(obj) {
			console.log(obj)
		},
		control() {
			this.show = false
		},
		submit() {
			if(this.address == ''){
				toast.showTitle('位置不能为空')
				return
			}
			
			if(this.user == ''){
				toast.showTitle('姓名不能为空')
				return
			}
			
			if(this.tel == ''){
				toast.showTitle('手机号不能为空')
				return
			}else if(!(/^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/.test(this.tel))){
				toast.showTitle('手机号格式不正确')
				return
			}
			
			if(this.content == ''){
				toast.showTitle('举报内容不能为空')
				return
			}
			
			if(this.content.length > 300){
				toast.showTitle('举报内容不能超过300字')
				return
			}
			
			if(this.imageData.length == 0){
				toast.showTitle('请上传图片')
				return
			}
			
			let self = this
			let arr = this.imageData
			for(let i=0;i<arr.length;i++){
				if(arr[i].indexOf(this.url) > -1){
					arr[i] = arr[i].substr(this.url.length)
				}
			}
			let files = arr.join('|')
			
			let datas = {
				uid: uni.getStorageSync('uid'),
				complType: this.radio,
				cname: this.user,
				telnum: this.tel,
				content: this.content,
				material: files,
				location: this.address,
				longitude: this.longitude,
				latitude: this.latitude
			}
			toast.showLoading('加载中...')
			let data = {
				url: '/complPhotograph/submit',
				data: datas,
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: function(res) {
					console.log(res)
					if (res.data.success == true) {
						toast.hideLoading()
						// self.clear()
						toast.showTitle('提交成功')
						setTimeout(function(){
							uni.navigateBack({
								delta: 1
							})
						},500)
					}
				}
			}
			ajax(data)
		}
	}
}
